.success {
  padding-top: 276px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.success-logo {
  display: inline-block;
  width: 60px;
  height: 60px;
  margin-bottom: 32px;
}

.success-text {
  font-size: 20px;
  line-height: 22px;
  color: #262627;
  margin-bottom: 32px;
}

.logo {
  height: 22px;
}

.logo > p {
  font-size: 16px;
  line-height: 22px;
  height: 22px;
  color: #262627;
}

.logo > p > a {
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;
  cursor: pointer;
  color: #262627;
}

.logo > p > a:hover {
  text-decoration: underline;
}


@media screen and (max-width: 1440px) {
  .success {
    padding-top: 168px;
  }

  .success-text {
    margin-bottom: 24px;
  }
}

@media screen and (max-width: 768px) and (min-width: 320px) {
  .success {
    padding-top: 1.68rem;
  }

  .success-text {
    margin-bottom: .24rem;
    font-size: .18rem;
  }

  .logo {
    /* width: 1.08rem; */
    height: .22rem;
  }
}

